import React from "react";
import '../style/projects.css';

import Weatherify from "../assets/weatherify.png";
import Achieve from "../assets/achieve.png";
import Ookla from "../assets/ookla.png";
import Sparke from "../assets/sparke.png";
import MiniRouge from "../assets/minirouge.png";
import CrossyBruin from "../assets/crossy.png";
import Basketball from "../assets/basketball.png";
import Hussle from "../assets/hussle.png";
import iiDecide from "../assets/iiDecide.png";

class ProjectModel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            hover: false
        };
        this.changeHover = this.changeHover.bind(this)
    }
    changeHover () {
        this.setState({ hover: !this.state.hover })
    }

    render() {
        const num_tags = this.props.tags.length
        return (
            <div className="project-container">
                <div className="project-inner">
                    <div className="project-front"> 
                        <img className="img" src={this.props.image} alt={this.props.link}/> 
                    </div>
                    <div className="project-back">
                        <p className="project-title">{this.props.title}</p>
                        <hr className="custom-hr" />
                        <p className="project-body">{this.props.description}</p>
                        <div className="tags">
                            {[...Array(num_tags)].map((x, k) =>
                                <p className="tag">{this.props.tags[k]}</p>)}
                         </div>
                        <td onClick={()=> window.open(this.props.link)}><p className="project-links">{this.props.linkDesc} »</p></td>
                    </div>
                </div>
            </div>
        )
    }
}
 

export class Projects extends React.Component {
    renderProject(title, image, description, link, linkDesc, tags) {
        return (
            <ProjectModel
                title={title}
                image={image}
                description={description}
                link={link}
                linkDesc={linkDesc}
                tags={tags}
            ></ProjectModel>
        );
    }
    
    render (){
        return (
            <div className="section-body">
                <p className="section-header"><u>Projects</u></p>
                <div className="parent">
                    {this.renderProject("Hussle", Hussle, "A freelance marketplace platform where college students can launch and offer side hustles to their community", "https://www.hussle.us/", "More Info", ["TypeScript", "MongoDB", "Express", "Node.js", "React Native"])}

                    {this.renderProject("Weatherify", Weatherify, "An iOS application that generates custom Spotify playlists and gives music recommendations based on the weather", "https://github.com/yililiu8/Weatherify", "GitHub", ["Swift", "Spotify Web API", "OpenWeatherMap"])}

                    {this.renderProject("iiDecide", iiDecide, "A platform for anonymously connecting victims and survivors of sexual assault", "https://www.novaforgood.org/work/iidecide", "More Info", ["Swift", "UIKit", "SwiftUI"])}
                     
                    {this.renderProject("Sparke", Sparke, "The Airbnb for parking - an iOS application where users can lend and rent private parking spaces", "https://github.com/ucladevx/InstaPark-ios", "GitHub", ["Swift", "Firebase", "BrainTree Payments API"])}

                    {this.renderProject("Basketball Simulation", Basketball, "A third-person basketball simulation implementing computer animation algorithms including inverse kinematics, symplectic euler integration, mass spring damper system, and Hermite splines", "https://github.com/yililiu8/cs174c-final-project", "GitHub", ["JavaScript", "TinyGraphics"])}
                
                    {this.renderProject("Achieve", Achieve, "An iOS application that allows users to keep track of personal goals and daily habits with 5000+ downloads", "https://apps.apple.com/us/app/id1522535100#?platform=iphone", "View on App Store", ["Swift", "iOS"])}

                    {this.renderProject("Crossy Bruins", CrossyBruin, "A 3D third-person game that lets you traverse through an endless path of obstacles and lanes as a Bruin.", "https://github.com/yililiu8/crossy-bruins", "GitHub", ["JavaScript", "TinyGraphics"])}
                    
                    {this.renderProject("Ookla Manager", Ookla, "A full-stack learning management web app for college students", "https://github.com/emilyvainberg12/CS97-Web-App-", "GitHub", ["JavaScript", "React.js", "Node.js", "MongoDB", "Express"])}
                                
                    {this.renderProject("Mini Rouge", MiniRouge, "A text-based Rouge inspired game where users can fight monsters, pick up artifacts, and traverse through levels", "https://github.com/yililiu8/Mini-Rouge", "Github", ["C++"])}

                </div>
                
            </div>
        );
    }
}